/* <========================= RESPONSIVE ==========================> */

/* =============== SUPER SMALL =============== */
@media screen and (max-width: 360px) {
  .status_modal .status_wrap {
    transform: translate(-50%, -30%) !important;
  }
}

@media screen and (max-width: 375px) {
  .status_modal .status_wrap {
    transform: translate(-50%, -45%) !important;
  }
}

/* =============== EXTRA SMALL =============== */
@media screen and (max-width: 576px) {
  .header {
    padding-bottom: 1rem !important;
  }

  .header .row .left {
    text-align: center;
  }

  .header .row .center {
    margin: 1rem 0;
  }

  .header .row .right ul {
    justify-content: space-around !important;
  }

  .header .row .right ul.show {
    width: 340px;
    transform: translate(33%, 45px) !important;
  }

  .notify-modal .body {
    height: 100% !important;
  }

  .header .row .right ul .user-username {
    margin: 0 0.5rem !important;
  }

  .message {
    margin: 0px;
  }

  .message .left_mess {
    padding-right: 0px;
  }

  .message .right_mess,
  .mess_detail .left {
    display: none;
  }

  .info__content__title .profile-follow {
    justify-content: center !important;
  }

  .info__content__title .setting-profile {
    padding: 0 !important;
  }

  .info__content__title .setting-profile button {
    font-size: 10px !important;
  }

  .follow_block {
    max-width: 100%;
    width: 340px !important;
    margin-top: 3rem;
  }

  .discover .width-905 {
    width: 100% !important;
  }

  .wrap-form {
    height: 100% !important;
    padding: 1rem;
  }

  .wrap-form > div {
    width: 100% !important;
  }

  .edit_profile form {
    margin: 170px auto !important;
  }

  .edit_profile form,
  .edit_profile .info_avatar {
    height: 450px !important;
    overflow: auto !important;
  }

  .status_modal .status_wrap {
    width: 100% !important;
  }
}

/* =============== SMALL =============== */
@media screen and (min-width: 576px) {
  .header {
    padding-bottom: 1rem !important;
  }

  .header .row .left {
    text-align: center;
  }

  .header .row .center {
    margin: 1rem 0;
  }

  .header .row .right ul {
    justify-content: space-around !important;
  }

  .header .row .right ul .user-username {
    margin: 0 0.5rem !important;
  }

  .notify-modal {
    width: 400px !important;
  }

  .notify-modal .body {
    height: 400px !important;
  }

  .discover .width-905 {
    width: 100% !important;
  }
}

/* =============== MEDIUM =============== */
@media screen and (min-width: 768px) {
  .header {
    padding-bottom: 0 !important;
  }

  .header .row .right ul .user-username {
    margin: 0 0.5rem !important;
  }

  .status_modal .status_wrap {
    width: 500px !important;
  }
}

/* =============== LARGE =============== */
@media screen and (min-width: 992px) {
  .header .row .left {
    text-align: left;
  }

  .header .row .right ul {
    justify-content: flex-end !important;
  }

  .discover .width-905 {
    width: 905px !important;
  }
}
